.containerHeadInf,
.containerHeadInfBegin {
  background-color: rgba(158, 249, 255, 0.75);
  min-width: 50vw;
  min-height: 50svh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 30px;
  align-items: center;
  border-radius: 20px;
  position: relative;
  gap: 20px;
}
.containerHeadInfBegin {
  background-color: transparent;
}
.containerHeadInfBegin {
  padding: 0;
}
.input {
  min-width: 20px;
  min-height: 20px;
  display: inline-block;
  opacity: 0.7;
}

.patientHeaderInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin-bottom: 100px;
}
.containerHeadInf h2,
.patientHeaderInfo h2,
.containerHeadInfBegin h2 {
  color: #003d8a;
  text-align: center;
}
.patientHeaderInfo img {
  max-width: 250px;
}
.patientHeaderInfo p {
  max-width: 500px;
  text-align: center;
  font-weight: bold;
  font-size: 20px;
}
.containerHeadInfBegin {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.footerButtons {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.firstVisitInfo {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 10px;
}
.informationBlock {
  border: 1px solid #003d8a;
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border-radius: 20px;
  cursor: pointer;
  transition: 0.5s;
}
.informationBlock:hover {
  transform: scale(1.05);
}
.currentVariants {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-column-gap: 30px;
  grid-row-gap: 10px;
}
.summaryBlock {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.summaryBlock ul {
  margin-left: 20px;
}
.summaryBlock h3 {
  margin-right: auto;
}

@media (max-width: 1150px) {
  .currentVariants {
    grid-template-columns: max-content;
  }
  .footerButtons {
    display: grid;
    grid-template-areas: "a b" "c c";
    gap: 10px;
  }
}

@media (max-width: 980px) {
  .firstVisitInfo {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 720px) {
  .currentVariants {
    grid-template-columns: 350px;
  }
  .informationBlockn:hover {
    transform: none;
  }
}
@media (max-width: 550px) {
  .containerHeadInf,
  .containerHeadInfBegin {
    padding: 20px 10px;
    gap: 15px;
  }
  .informationBlock {
    padding: 10px 20px;
  }
  .informationBlock div {
    text-align: center;
  }
  .footerButtons {
    margin-top: 5vh;
  }
}
@media (max-width: 400px) {
  .currentVariants {
    grid-template-columns: 300px;
  }
}
