.App {
  display: flex;
  min-height: 100svh;
  align-items: center;
  justify-content: center;
  background-image: url("./Images/fone.png");
  background-position: center;
  background-size: cover;
  padding: 20px 5px;
}
.buttonNext,
.buttonBack {
  border: none;
  padding: 10px 25px;
  font-size: 18px;
  background-color: rgb(211, 162, 0);
  border-radius: 15px;
  color: white;
  cursor: pointer;
}
.buttonBack {
  background-color: #004390a1;
  grid-area: a;
}
.buttonNext {
  grid-area: b;
}
.commentary-button {
  background-color: #003e8a75;
  grid-area: c;
}
img {
  max-width: 100%;
  opacity: 0.9;
}
